import React, {useState, useEffect} from 'react';
import axios from 'axios'
import './App.css';
import {Route, Switch, Redirect, BrowserRouter} from 'react-router-dom'
import Messenger from './messenger';
import LogInPage from './components/logInPage';
import RegPage from './components/RegPage';
import Loader from './components/Loader';

function App() {
  // const [currentDialog, setCurrentDialog] = useState(0)
  const [user, setUser] = useState('loading')
  const [flashMsg, setFlashMsg] = useState('')
  

  /* Dev Test Time */
  const dialogs = [
    {id: 1, name: 'spareack', photoURL: undefined, talks: [{id: 1, name:'о мессенджере'}, {id:2, name: 'что?'}, {id: 3, name:'хи хи'}]}, 
    {id: 2, name: 'zxchrnk', photoURL: undefined, talks: [{id: 1, name:'Учёба сраная'}, {id: 2, name: 'Дима крутой'}]}
]
  const dialogs2 = [
    {id: 1, name: 'spareack', photoURL: undefined}, 
    {id: 2, name: 'zxchrnk', photoURL: undefined}
  ]
  /* Dev Test Time */
    useEffect(() => {
      axios({
        method: 'get',
        url: '/is_authorized',
      }).then(res => {
          if(res.data.status == 0){
              setUser(res.data.is_auth)
              console.log(res.data)
          }
      }).catch(error => console.log(error))
    }, [])
  if(user === 'loading'){
    return (<div style={{height: '100vh',
                        backgroundColor: 'black'}}><Loader/></div>)
  } else return (
    <BrowserRouter>
      {user ? 
      (
        <Switch>
          <Route key={'/talk'} exact={true} path={'/talk'} component={() =>( <Messenger dialogs={dialogs} setLoggedOut={setUser}/>)} />
           <Redirect to={'/talk'} />
        </Switch>
      )
      :
      (
        <Switch>
          <Route key={'/registration'} exact={true} path={'/registration'} component={() => (<RegPage setFlash={setFlashMsg}/>)} />
          <Route key={'/login'} exact={true} path={'/login'} component={() => (<LogInPage setLoggedIn={setUser} setFlash={setFlashMsg} flash={flashMsg}/>)}/>
           <Redirect to={'/login'} />
        </Switch>
      )}
    </BrowserRouter>
  );
}

export default App;
